





































import Vue from 'vue';
import Workout from '@/models/Workout';
import uuid from 'uuid';

export default Vue.extend({
  name: 'WorkoutForm',
  props: {
    workout: {
      default: () => <Workout>{
        id: uuid(),
        date: new Date()
      }
    }
  },
  methods: {
    save() {
      this.$store.commit('save', this.$props.workout);
      this.$router.push('/');
    }
  },
  data () {
    return {
      exercises: [
        'Bench Press',
        'Dead Lift',
      ]
    }
  },
});
